import React from 'react'
import moment from 'moment'
import { Bar } from 'react-roughviz'
import { IUserStatistic } from 'models'

interface IProps {
  data: IUserStatistic[]
}

const UsersGraph: React.FC<IProps> = ({ data }) => {
  const labels = data.map(({ day }) => day)
  const values = data.map(({ count }) => count)
  const month = moment().format('MMMM')
  return (
    <>
      <h3>Users statistic for {month}</h3>
      <Bar width={1000} data={{ labels, values }} labels="flavor" values="price" />
    </>
  )
}

export default UsersGraph
