import { api } from 'config'
import { IUser, IUserStatistic } from 'models/user'
import UserStore from 'stores/user'

const prefix = '/user'

export async function addUser(user: Partial<IUser>): Promise<IUser> {
  const res = await api.post(prefix, user)
  return res.data
}

export async function createBulk(fileName: string): Promise<IUser[]> {
  const res = await api.post(`${prefix}/bulk/${fileName}`)
  return res.data
}

export async function getInfo() {
  const res = await api.get(`${prefix}/info`)
  UserStore.setUser(res.data)
}

export async function getUsers(): Promise<IUser[]> {
  const res = await api.get(prefix)
  return res.data
}

export async function getUser(id: IUser['_id']): Promise<IUser> {
  const res = await api.get(`${prefix}/${id}`)
  return res.data
}

export async function getStatistic(): Promise<IUserStatistic[]> {
  const res = await api.get(`${prefix}/statistic`)
  return res.data
}

export async function setUser(id: IUser['_id'], user: Partial<IUser>): Promise<IUser> {
  const res = await api.patch(`${prefix}/${id}`, user)
  return res.data
}

export async function deleteUser(id: IUser['_id']) {
  const res = await api.delete(`${prefix}/${id}`)
  return res.data
}

export async function register(user: Partial<IUser>): Promise<IUser> {
  const res = await api.post(`${prefix}/register`, user)
  return res.data
}

export async function login(email: IUser['email'], password: string) {
  const res = await api.post(`${prefix}/login?email=${email}&password=${password}`)
  const { token, ...user } = res.data
  localStorage.setItem('token', token)
  UserStore.setUser(user)
  window.location.href = '/'
}
