import React, { createContext, useContext, Component } from 'react'
import { configure, observable } from 'mobx'
import { Observer } from 'mobx-react'
import remotedev from 'mobx-remotedev'
import userStore from './user'

configure({ enforceActions: 'observed' })

@remotedev({ global: true })
export class RootStore {
  @observable user = userStore
}

const store = new RootStore()

export const StoreContext = createContext<RootStore>(new RootStore())

export const useStore = (): RootStore => {
  const store = useContext(StoreContext)
  if (!store) throw new Error('You have forgot to use StoreProvider, shame on you.')
  return store
}

export const StoreProvider: React.FC = ({ children }) => {
  const store = useStore()
  return <StoreContext.Provider value={store}>{children}</StoreContext.Provider>
}

type TMapStoreToProps = (store: RootStore) => Partial<RootStore>

export const connect = (mapStoreToProps: TMapStoreToProps): Function => {
  return function(WrappedComponent: any): React.ElementType {
    return class extends Component {
      static displayName = `connect(${WrappedComponent.displayName ||
        WrappedComponent.name ||
        'Component'})`

      render() {
        return (
          <Observer>
            {() => <WrappedComponent {...this.props} {...mapStoreToProps(store)} />}
          </Observer>
        )
      }
    }
  }
}

export default store
