import React, { Component } from 'react'
import { notification, Spin } from 'antd'
import Graph from './Graph'
import * as userActions from 'actions/user'
import { IUserStatistic } from 'models'

interface IState {
  graphData?: IUserStatistic[]
}

class Users extends Component<{}, IState> {
  state: IState = {}
  componentDidMount() {
    this.getData()
  }

  getData = async () => {
    try {
      const graphData = await userActions.getStatistic()
      this.setState({ graphData })
    } catch (e) {
      console.log('e', e)
      notification.error({ message: e.message || e })
    }
  }

  render() {
    const { graphData } = this.state
    return graphData ? <Graph data={graphData} /> : <Spin size="large" />
  }
}

export default Users
