import { observable, action, computed } from 'mobx'
import { IUser } from 'models'

class UserStore {
  @observable user?: IUser = undefined

  @action
  setUser = (user: IUser) => {
    this.user = user
  }

  @computed
  get fullName() {
    return this.user ? this.user.firstName + this.user.lastName : ''
  }

  @action
  unserUser = () => {
    this.user = undefined
  }
}

export default new UserStore()
