import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import { Form, Input, Icon, Button, notification } from 'antd'
import * as userActions from 'actions/user'

import styles from './styles.module.scss'

interface ICreds {
  login?: string
  password?: string
}

const Login: React.FC = () => {
  const [creds, setCreads] = useState<ICreds>({})

  function changeCreds({ target: { name, value } }: React.ChangeEvent<HTMLInputElement>) {
    setCreads({ ...creds, [name]: value })
  }

  async function handleSubmit() {
    const { login, password } = creds
    if (!login || !password) return

    try {
      await userActions.login(login, password)
    } catch (e) {
      notification.error({ message: 'Not valid credentionals' })
    }
  }

  return (
    <Form className={styles.form}>
      <Form.Item>
        <Input
          prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
          placeholder="Username"
          name="login"
          onChange={changeCreds}
        />
      </Form.Item>
      <Form.Item>
        <Input
          prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
          type="password"
          name="password"
          placeholder="Password"
          onChange={changeCreds}
        />
      </Form.Item>
      <Form.Item>
        <Button block onClick={handleSubmit}>
          Login
        </Button>
        Or <Link to="/register">register now!</Link>
      </Form.Item>
    </Form>
  )
}

export default Login
