import React, { useState } from 'react'
import { CardElement, useStripe, useElements } from '@stripe/react-stripe-js'
import { Form, Button, Layout, Input, notification } from 'antd'
import * as paymentsActions from 'actions/payments'

import styles from './styles.module.scss'

const CheckoutForm: React.FC = () => {
  const [amount, setAmount] = useState(10)
  const stripe = useStripe()
  const elements = useElements()

  const changeAmount = ({ target }: React.ChangeEvent<HTMLInputElement>) => {
    setAmount(+target.value)
  }

  const handleSubmit = async (
    e: React.FormEvent<HTMLFormElement> | React.MouseEvent<HTMLElement, MouseEvent>
  ) => {
    e.preventDefault()
    try {
      if (!stripe || !elements) return

      const cardElement = elements.getElement(CardElement)
      if (!cardElement) return

      // Use your card Element with other Stripe.js APIs
      const { token } = await stripe.createToken(cardElement)
      if (!token) return

      await paymentsActions.payment(token.id, amount)
    } catch (e) {
      notification.error(e.message)
    }
  }

  return (
    <Layout className={styles.container}>
      <Form onSubmit={handleSubmit} className={styles.form}>
        <Input type="number" placeholder="Amount" value={amount} onChange={changeAmount} />
        <CardElement
          options={{
            style: {
              base: {
                fontSize: '16px',
                color: '#424770',
                '::placeholder': {
                  color: '#aab7c4',
                },
              },
              invalid: {
                color: '#9e2146',
              },
            },
          }}
        />
        <Button onClick={handleSubmit} type="primary" block>
          Pay ${amount}
        </Button>
      </Form>
    </Layout>
  )
}

export default CheckoutForm
