import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import _ from 'lodash'
import { Form, Input, Button, notification } from 'antd'
import { register } from 'actions/user'

import styles from './styles.module.scss'
import { RouterProps, WithRouterProps, RouteComponentProps } from 'react-router'

interface ICreds {
  firstName?: string
  lastName?: string
  email?: string
}

const Register: React.FC<RouteComponentProps> = props => {
  const [creds, setCreads] = useState<ICreds>({})
  const [loading, setLoading] = useState<boolean>(false)

  function changeCreds({ target: { name, value } }: React.ChangeEvent<HTMLInputElement>) {
    setCreads({ ...creds, [name]: value })
  }

  function handleSubmit() {
    setLoading(true)
    try {
      register(creds)
      props.history.push('/login')
    } catch (e) {
      notification.error({ message: e.message })
    } finally {
      setLoading(false)
    }
  }

  return (
    <Form className={styles.form}>
      {['firstName', 'lastName', 'email', 'password'].map(field => (
        <Form.Item>
          <Input name={field} placeholder={_.startCase(field)} onChange={changeCreds} />
        </Form.Item>
      ))}
      <Form.Item>
        <Button block onClick={handleSubmit}>
          Register
        </Button>
        Or <Link to="/login">Login!</Link>
      </Form.Item>
    </Form>
  )
}

export default Register
