import React, { useState } from 'react'
import { Modal, Form, Input, Icon, Select, notification, message } from 'antd'
import * as userActions from 'actions/user'
import { IUser } from 'models/user'

const { Option } = Select

interface IProps {
  open?: boolean
  edit?: IUser
  loadUsers: () => void
  onClose: () => void
}

const UsersModal: React.FC<IProps> = ({ edit, open, onClose, loadUsers }) => {
  const [user, setUser] = useState<Partial<IUser>>(edit || {})

  if (edit && edit._id !== user._id) setUser(edit)
  if (!edit && user._id) setUser({})

  function changeUser({ target: { name, value } }: React.ChangeEvent<HTMLInputElement>) {
    setUser({ ...user, [name]: value })
  }

  async function handleSubmit() {
    const closeMessage = message.loading({ content: 'Loading...' })
    try {
      edit ? await userActions.setUser(edit._id, user) : await userActions.addUser(user)
      await loadUsers()
      onClose()
    } catch (e) {
      notification.error({ message: e.message || e })
    } finally {
      closeMessage()
      message.success('Success!')
    }
  }

  return (
    <Modal
      title={(edit ? 'update ' : 'craete ') + 'user'}
      visible={open}
      onOk={handleSubmit}
      onCancel={onClose}
    >
      <Form>
        <Form.Item>
          <Input
            name="firstName"
            prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
            placeholder="First name"
            value={user.firstName}
            onChange={changeUser}
          />
          <Input
            name="lastName"
            prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
            placeholder="Last name"
            value={user.lastName}
            onChange={changeUser}
          />
          <Input
            name="email"
            prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
            placeholder="email"
            value={user.email}
            onChange={changeUser}
          />
          <Select
            value={user.role || 'user'}
            onChange={(value: IUser['role']) => setUser({ ...user, role: value })}
          >
            <Option value="user">User</Option>
            <Option value="admin">Admin</Option>
          </Select>
        </Form.Item>
      </Form>
    </Modal>
  )
}

export default UsersModal
