import React, { Component } from 'react'
import { List, notification, Spin, Button, Icon, message, Upload } from 'antd'
import { UploadFile, UploadChangeParam } from 'antd/lib/upload/interface'
import CsvImportModal from './CsvImportModal'
import Modal from './Modal'
import * as userActions from 'actions/user'
import { IUser } from 'models/user'
import { host } from 'config'

import styles from './styles.module.scss'

interface IState {
  users?: IUser[]
  loading: boolean
  modal: {
    open: boolean
    user?: IUser
  }
  csvModal: {
    open: boolean
    users?: IUser[]
  }
}

class Users extends Component<{}, IState> {
  state: IState = {
    loading: true,
    modal: { open: false },
    csvModal: { open: false },
  }

  componentDidMount() {
    this.loadData()
  }

  changeModal = (user?: IUser) => {
    const { open } = this.state.modal
    this.setState({ modal: { open: !open, user } })
  }

  changeCsvModal = (open: boolean, users?: IUser[]) => {
    this.setState({ csvModal: { open, users } })
  }

  changeFile = async (info: UploadChangeParam<UploadFile<any>>) => {
    console.log('info', info)
    const { status, response } = info.file
    if (status === 'uploading') this.changeCsvModal(true)
    if (status === 'done' && response) {
      try {
        const users = await userActions.createBulk(response)
        this.changeCsvModal(true, users)
      } catch (e) {
        notification.error({ message: e.message })
      } finally {
        this.loadData()
      }
    }
  }

  deleteUser = async (id: IUser['_id']) => {
    try {
      await userActions.deleteUser(id)
      this.loadData()
    } catch (e) {
      notification.error({ message: e.message })
    }
  }

  loadData = async () => {
    const closeMessage = message.loading({ content: 'Loading...' })
    try {
      const users = await userActions.getUsers()
      this.setState({ users, loading: false })
    } catch (e) {
      notification.error({ message: e.message || e })
    } finally {
      closeMessage()
    }
  }

  render() {
    const { users, modal, csvModal } = this.state

    return (
      <div>
        <div className={styles.btnGroup}>
          <Button icon="plus" type="primary" onClick={() => this.changeModal()}>
            Add User
          </Button>
          <Upload
            multiple={false}
            name="file"
            action={`${host}file`}
            accept=".csv"
            onChange={this.changeFile}
          >
            <Button icon="upload">Import csv</Button>
          </Upload>
        </div>
        <List
          dataSource={users}
          renderItem={user => (
            <List.Item
              key={user._id}
              actions={[
                <Icon type="edit" onClick={() => this.changeModal(user)} />,
                <Icon type="delete" onClick={() => this.deleteUser(user._id)} />,
              ]}
            >
              <List.Item.Meta title={<p>{user.fullName}</p>} description={user.email} />
            </List.Item>
          )}
        >
          {this.state.loading && <Spin />}
        </List>
        <CsvImportModal
          open={csvModal.open}
          data={csvModal.users}
          onClose={() => this.changeCsvModal(false)}
        />
        <Modal
          loadUsers={this.loadData}
          open={modal.open}
          edit={modal.user}
          onClose={() => this.changeModal()}
        />
      </div>
    )
  }
}

export default Users
