import React from 'react'
import { Modal, List, Spin, Alert } from 'antd'
import { IUser } from 'models'

interface IProps {
  open: boolean
  data?: IUser[]
  onClose: () => void
}

const btnProps = { style: { display: 'none' } }

const CsvImportModal: React.FC<IProps> = ({ open, data, onClose }) => {
  return (
    <Modal
      visible={open}
      title="Import data from a csv file..."
      okButtonProps={!data ? btnProps : { onClick: onClose }}
      cancelButtonProps={btnProps}
    >
      {data ? (
        <Alert message="Done" description="Users have been imported." type="info" />
      ) : (
        <Spin tip="Loading...">
          <Alert message="Please wait" description="Users are importing." type="info" />
        </Spin>
      )}
    </Modal>
  )
}

export default CsvImportModal
